import React, { useEffect } from 'react';
import { analyticsService } from '@wk/acm-analytics';

import { formTypeHelpData } from './form-type-help-hint.data';
import { Accordion, AccordionItem } from '../../accordion';

import styles from './form-type-help-hint-content.scss';

const FormTypeHelpHintContentComponent: React.FC = () => {
    useEffect(() => {
        analyticsService.onWidgetEvent({
            widgetId: 'formTypeHelpPopup',
            action: 'open',
            subType: 'popup',
        });
    }, []);

    return (
        <>
            {formTypeHelpData.map((section, sectionIndex) => (
                <div className={styles['form-type-help-content__section']} key={section.title + sectionIndex}>
                    <Accordion title={section.title}>
                        {section.groups.map((group, groupIndex) => (
                            <AccordionItem key={group.title + sectionIndex + groupIndex} title={group.title}>
                                {
                                    <ul className={styles['form-type-help-content__list']}>
                                        {group.items.map((item, itemIndex) => (
                                            <li
                                                key={item.title + sectionIndex + groupIndex + itemIndex}
                                                className={styles['form-type-help-content__list-item']}>
                                                <span
                                                    className={styles['form-type-help-content__list-item_text-green']}>
                                                    {item.title}
                                                </span>
                                                {item.description ? ` - ${item.description}` : ''}
                                            </li>
                                        ))}
                                    </ul>
                                }
                            </AccordionItem>
                        ))}
                    </Accordion>
                </div>
            ))}
        </>
    );
};

export const FormTypeHelpHintContent = React.memo(FormTypeHelpHintContentComponent);
